import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  ViewChild,
  ChangeDetectorRef,
  HostListener
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { environment } from '@env/environment';

import { TABS } from '@shared/constants/property-details';

import { PropertyService, UsersService } from '@shared/api';
import { Property } from '@shared/api/property/property.model';
import {
  PropertySummary,
  FinancialOverview,
  FinancialIncome,
  FinancialCapitalDetails,
  FinancialCapitalGrowth,
  PropertyLocation,
  FloorPlanLayout,
  Timeline,
  Video,
  FinancialExpense
} from '@shared/api/property-details/property-details.model';
import { PhotoGalleryService } from '@shared/services/photo-gallery/photo-gallery.service';
import { PhotoCarouselService } from '@shared/services/photo-carousel/photo-carousel.service';
import { PropertyDetailsService } from '@shared/api/property-details/property-details.service';
import {
  PropertyImages,
  PropertyAssetTypes
} from '@shared/api/property-details/property-details.model';
import { NgbCarousel } from '@ng-bootstrap/ng-bootstrap';
import { PropertySharedService } from '@shared/services';

import { PROPERTY_STATUS } from '@shared/constants/property-details';
import { SessionStorageService } from '@shared/services/session-storage/session-storage.service';

@Component({
  selector: 'app-property-details',
  templateUrl: './property-details.component.html',
  styleUrls: ['./property-details.component.scss']
})
export class PropertyDetailsComponent implements OnInit {
  activeTabs;
  @ViewChild('imagesCarousel')
  imageCarousel: NgbCarousel;

  images: string[];
  propertyId: number;
  propertyDetails: Property;
  summaryDetails: PropertySummary;
  summaryPrivateDocs: PropertyImages;
  testInput: 'string';
  propertyImages: PropertyImages;
  propertyAssetTypes: PropertyAssetTypes[];
  baseUrl = environment.baseUrl;
  desktopView = true;
  tabletViewMaxSize = 768;
  propertyAddress;
  propertyLocation: PropertyLocation;
  floorPlanLayout: FloorPlanLayout;
  video: Video;

  // Financial Tabs
  financialOverview: FinancialOverview;
  financiaIncome: FinancialIncome;
  financialExpense: FinancialExpense;
  financialCapitalDetails: FinancialCapitalDetails[];
  financialCapitalGrowth: FinancialCapitalGrowth[];
  timelines: Timeline[];

  @Output()
  openImageGallery = new EventEmitter();

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this.desktopView = window.innerWidth >= this.tabletViewMaxSize;
      this.changeDetectorRef.detectChanges();
    }, 500);
  }

  constructor(
    private router: Router,
    private activeRoute: ActivatedRoute,
    private _propertyService: PropertyService,
    private _photoGalleryService: PhotoGalleryService,
    private _propertyDetailsService: PropertyDetailsService,
    private changeDetectorRef: ChangeDetectorRef,
    private _propertyShared: PropertySharedService,
    private _photoCarouselService: PhotoCarouselService,
    private _usersService: UsersService,
    private _sessionStorageService: SessionStorageService
  ) {}

  ngOnInit() {
    if (!this.activeRoute.snapshot.params.hasOwnProperty('id')) {
      this.router.navigate(['/app/properties']);
    } else {
      this.checkEAC();
      this._photoCarouselService.updateImageIndex.subscribe(index => {
        this.imageCarousel.select(`img-${index}`);
      });
      this.activeTabs = TABS.SUMMARY;
      this.propertyId = this.activeRoute.snapshot.params.id || 0;
      this.getDetails(this.propertyId);
      this.getPropertyAssetTypes();
      this.getFloorPlanLayout(this.propertyId);
      this.getTimelines(this.propertyId);
      this.getVideo(this.propertyId);
      this.getSummaryDetails(this.propertyId);
      this.desktopView = window.innerWidth >= this.tabletViewMaxSize;

      this.getFincancialOverview();
      this.getFinancialIncome();
      this.getFinancialExpense();
      this.getFinancialCapitalGrowth();
      this.getFinancialCapitalDetails();
      this.getPropertyLocation();
    }
  }

  changeActiveTabs(idx) {
    this.activeTabs = idx;
  }

  setTabIndex(event) {
    this.activeTabs = event.index;
  }

  getDetails(propertyId) {
    this._propertyService.getPropertyDetails(propertyId).subscribe(
      data => {
        if (data.propertyListingTypeId === PROPERTY_STATUS.COMING_SOON) {
          this.router.navigate(['/app/properties']);
        }
        this.propertyDetails = data;
        this.propertyAddress = this.propertyDetails.propertyAddress;
      },
      () => {
        this.router.navigate(['/app/properties']);
      }
    );
  }

  getSummaryDetails(propertyId) {
    this._propertyDetailsService
      .getPropertySummary(propertyId)
      .subscribe(data => {
        const overviewArr = data.overview && data.overview.split('\\n');
        const locationSummaryArr =
          data.locationSummary && data.locationSummary.split('\\n');
        const businessCaseArr =
          data.businessCase && data.businessCase.split('\\n');

        this.summaryDetails = {
          ...data,
          overviewArr,
          locationSummaryArr,
          businessCaseArr
        };

        this._propertyDetailsService
          .getPropertyImages({ propertyId, propertyAssetTypeId: 5 })
          .subscribe(res => {
            if (res) {
              this.summaryPrivateDocs = res[0];
            }
          });
      });
  }

  getPropertyImages(detailIds) {
    this._propertyDetailsService
      .getPropertyImages(detailIds)
      .subscribe(data => {
        this.propertyImages = data;
        this.generateSlideIds(this.propertyImages);
      });
  }

  getPropertyAssetTypes() {
    this._propertyDetailsService.getPropertyAssetTypes().subscribe(data => {
      this.propertyAssetTypes = data;

      this.getPropertyImages({
        propertyAssetTypeId: this.getPropertyAssetTypeId('Property Photo'),
        propertyId: this.propertyId
      });
    });
  }

  purchaseItem(id: number) {
    this.router.navigate([`/preorder/${id}`]);
  }

  showImageGallery(id = 0) {
    this._photoGalleryService.toggle();
    this._photoGalleryService.setImages(this.propertyImages);
    this._photoGalleryService.setShowThumbnails(true);
    this._photoGalleryService.selectedImage(id);
    this._photoCarouselService.setImageIndex(id);
  }

  concatBaseUrl(url) {
    return this.baseUrl.concat(url);
  }

  getPropertyAssetTypeId(assetTypeName) {
    const selectedAssetType = this.propertyAssetTypes.filter(
      assetType => assetType.propertyAssetTypeName === assetTypeName
    );
    return (
      selectedAssetType.length > 0 && selectedAssetType[0].propertyAssetTypeId
    );
  }

  generateSlideIds(images) {
    images.map((image, index) => {
      image.slideId = index;
    });
  }

  arrowSlide(direction, event) {
    direction === 'left'
      ? this.imageCarousel.prev()
      : this.imageCarousel.next();
    event.stopPropagation();
  }

  isPropertyPreOrder() {
    return (
      this.propertyDetails.propertyListingTypeId === PROPERTY_STATUS.PRE_ORDER
    );
  }

  // Financial Details

  getFincancialOverview() {
    this._propertyDetailsService
      .getFinancialOverview(this.propertyId)
      .subscribe(data => {
        this.financialOverview = data;
      });
  }

  getFinancialIncome() {
    this._propertyDetailsService
      .getFinancialIncome(this.propertyId)
      .subscribe(data => {
        this.financiaIncome = data;
      });
  }

  getFinancialExpense() {
    this._propertyDetailsService
      .getFinancialExpense(this.propertyId)
      .subscribe(data => {
        this.financialExpense = data;
      });
  }

  getFinancialCapitalGrowth() {
    this._propertyDetailsService
      .getFinancialCapitalGrowth(this.propertyId)
      .subscribe(data => {
        this.financialCapitalGrowth = data;
      });
  }

  getFinancialCapitalDetails() {
    this._propertyDetailsService
      .getFinancialCapitalDetails(this.propertyId)
      .subscribe(data => {
        this.financialCapitalDetails = data;
      });
  }

  getPropertyLocation() {
    this._propertyDetailsService
      .getPropertyLocation(this.propertyId)
      .subscribe(data => {
        this.propertyLocation = data;
      });
  }

  getFloorPlanLayout(id) {
    this._propertyDetailsService.getFloorPlanLayout(id).subscribe(data => {
      this.floorPlanLayout = data;
    });
  }

  getTimelines(id) {
    this._propertyDetailsService.getTimelines(id).subscribe(data => {
      this.timelines = data;
    });
  }

  getVideo(id) {
    this._propertyDetailsService.getVideo(id).subscribe(data => {
      this.video = data;
    });
  }

  checkEAC() {
    this._sessionStorageService.removeSession('EAC');
    const url = this.activeRoute.snapshot.queryParams;
    if (url.hasOwnProperty('EAC') && url.EAC === '1') {
      this._sessionStorageService.setSession('EAC', 1);
    }
  }
}
