import { Component, OnInit, HostListener } from '@angular/core';

import { PropertyService } from '@shared/api';
import { Router } from '@angular/router';

import { environment } from '@env/environment';
import { PROPERTY_STATUS } from '@shared/constants/property-details';

@Component({
  selector: 'app-properties',
  templateUrl: './properties.component.html',
  styleUrls: ['./properties.component.scss']
})
export class PropertiesComponent implements OnInit {
  baseUrl = environment.baseUrl;
  filterCollapse: boolean = false;

  countrySortOptions = [];
  latestListingOptions = [
    { value: 'DateDesc', label: 'Latest Listing' },
    { value: 'DateAsc', label: 'Oldest Listing' },
    { value: 'PriceAsc', label: 'Lowest Price' },
    { value: 'PriceDesc', label: 'Highest Price' },
    { value: 'RentalYieldAsc', label: 'Lowest Rental Yield' },
    { value: 'RentalYieldDesc', label: 'Highest Rental Yield' }
  ];

  countrySortSelected = {};
  latestListingSelected = {};
  properties;
  propertiesForMap;
  pageNumber = 1;
  pageSize = 6;
  propertiesParams: { [key: string]: any } = {};
  mapClose;
  isForPropertyDetails = false;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    if (event.target.innerWidth <= 767) {
      this.filterCollapse = true;
    }
  }

  constructor(
    private router: Router,
    private _propertyService: PropertyService
  ) {
    if (window.screen.width <= 767) {
      this.filterCollapse = true;
    }
  }

  ngOnInit() {
    this.init();
  }

  init() {
    this.mapClose = true;
    this.countrySortOptions = [];
    this.countrySortOptions.push({
      countryId: 0,
      countryName: 'All Countries',
      stampDuty: 0.0,
      capitalGrowth: 0.0
    });
    this.countrySortSelected = this.countrySortOptions[0].countryId;
    this.latestListingSelected = this.latestListingOptions[0].value;
    this.updatePropertiesFilter(
      this.countrySortSelected,
      this.latestListingSelected
    );
    this.getCountry();
  }

  getCountry() {
    this._propertyService.getCountry().subscribe(data => {
      this.countrySortOptions = this.countrySortOptions.concat(data);
    });
  }

  getProperties(params) {
    this._propertyService.getProperties(params).subscribe(data => {
      this.properties = data;
      this.propertiesForMap = this.properties.filter(
        i => i.propertyListingTypeId !== PROPERTY_STATUS.COMING_SOON
      );
    });
  }

  updatePropertiesFilter(countryId, sortingMode) {
    this.propertiesParams = {};
    this.propertiesParams = {
      pageNumber: this.pageNumber,
      pageSize: this.pageSize,
      sortingMode: sortingMode
    };
    if (!!countryId) {
      this.propertiesParams.countryId = countryId;
    }
    this.getProperties(this.propertiesParams);
  }

  purchaseItem() {
    this.router.navigate(['/app/purchase']);
  }

  viewDetails(id, disabled = false) {
    if (disabled) {
      return;
    }
    // this.router.navigate([`/app/property/${id}`]);
  }

  preOrder(id) {
    this.router.navigate([`/preorder/${id}`]);
  }

  toggleFilter() {
    this.filterCollapse = !this.filterCollapse;
  }

  isPropertyComingSoon(status) {
    return status === PROPERTY_STATUS.COMING_SOON;
  }

  isPropertyPreOrder(status) {
    return status === PROPERTY_STATUS.PRE_ORDER;
  }
}
