import { Component, OnInit, Input, ViewChild } from '@angular/core';
import {
  FinancialCapitalDetails,
  FinancialCapitalGrowth,
  FinancialExpense
} from '@shared/api/property-details/property-details.model';

import { Chart } from 'chart.js';

import { numberAbbreviator } from '@shared/utilities/numberFormatter';

@Component({
  selector: 'tab-financial-details',
  templateUrl: './tab-financial-details.component.html',
  styleUrls: ['./tab-financial-details.component.scss']
})
export class TabFinancialDetailsComponent implements OnInit {
  propertyValue;
  capitalDetails;
  capitalGrowth;
  capitalList;
  capitalGrowthYears;
  expenseDetails;
  totalExpense;

  @Input()
  overviewDetails;
  @Input()
  incomeDetails;
  @Input()
  set expensesList(list: FinancialExpense[]) {
    this.expenseDetails = list;
    this.totalExpense = (list && list.length && list[0].totalExpense) || 0;
  }
  @Input()
  set capitalDetailsList(list: FinancialCapitalDetails[]) {
    this.capitalDetails = list;
    this.propertyValue = (list && list.length && list[0].propertyValue) || 0;
  }

  @ViewChild('myChart') private myChart;
  chart;

  @Input()
  set capitalGrowthList(list: Array<FinancialCapitalGrowth>) {
    if (!list || !list.length) {
      return;
    }
    this.capitalGrowthYears = list.length;
    this.capitalList = list;
    this.capitalGrowth = list[0] || {};
  }

  isCollapsed: boolean = false;

  constructor() {}

  ngOnInit() {}
}
